import React, { useEffect, useState } from 'react'
import {
    Row, Col, Button,
    Space, Collapse, DatePicker, Select
} from 'antd';

import {
    SearchOutlined,
    ReloadOutlined
} from '@ant-design/icons';
import moment from 'moment';
import Api from '../../helpers/api';
import handleApiError from '../../helpers/handleApiError';

const { RangePicker } = DatePicker;
const { Option } = Select;
const api = new Api;

const FilterList = (props) => {

    const [code, setCode] = useState([])
    const [codeDo, setCodeDo] = useState([])
    const [plant, setPlant] = useState([])
    const [room, setRoom] = useState([])
    const [vendor, setVendor] = useState([])
    const [valueSearch, setValueSearch] = useState({
        code: [],
        code_do: [],
        plant_id: [],
        room_id: [],
        vendor_id: [],
        created_at: [],
        type: [],
    })

    useEffect(() => {
        fetchPlant()
        fetchRoom()
        fetchVendor()
        fetchCode()
        fetchCodeDo()
        getDataBySavedFilter()
    }, [])

    // plant
    const fetchPlant = (q) => {
        api.getPlant({ q: q }).then(response => {
            const { data } = response.data;
            setPlant(data);
        }).catch(err => {
            console.log(err)
            handleApiError(err, props)
        })
    };

    const handleSearchPlant = (value) => {
        fetchPlant(value)
    }

    // room
    const fetchRoom = (q) => {
        api.getRoom({ q: q }).then(response => {
            const { data } = response.data;
            setRoom(data);
        }).catch(err => {
            console.log(err)
            handleApiError(err, props)
        })
    };

    const handleSearchRoom = (value) => {
        fetchRoom(value)
    }

    // vendor
    const fetchVendor = (q) => {
        api.getVendor({ q: q }).then(response => {
            const { data } = response.data;
            setVendor(data);
        }).catch(err => {
            console.log(err)
        })
    };

    const handleSearchVendor = (value) => {
        fetchVendor(value)
    }

    // code
    const fetchCode = (q) => {
        api.getReservation({ q: q }).then(response => {
            const { data } = response.data;
            setCode(data);
        }).catch(err => {
            console.log(err)
        })
    };

    const handleSearchPo = (value) => {
        fetchCode(value)
    }

    // code do
    const fetchCodeDo = (q) => {
        api.getDeliveryOrder({ q: q }).then(response => {
            const { data } = response.data;
            setCodeDo(data);
        }).catch(err => {
            console.log(err)
        })
    };

    const handleSearchDo = (value) => {
        fetchCodeDo(value)
    }

    const handleInput = (value, name) => {
        setValueSearch({ ...valueSearch, [name]: value })
    }

    const getDataBySavedFilter = () => {
        const dataLocalStorage = JSON.parse(localStorage.getItem('filter_delivery_order'))
        if (dataLocalStorage) {
            const filtersKeys = Object.keys(dataLocalStorage).length
            if (filtersKeys > 0) {
                if (dataLocalStorage.delivery_date) {
                    dataLocalStorage.delivery_date.map((item, index) => {
                        dataLocalStorage.delivery_date[index] = moment.utc(item, 'YYYY-MM-DD')
                    })
                }
                setValueSearch(dataLocalStorage)
                props.filter(dataLocalStorage)
            }
        }
    }

    const handleFilter = () => {
        if (valueSearch.delivery_date) {
            valueSearch.delivery_date.map((item, index) => {
                valueSearch.delivery_date[index] = moment.utc(item, 'YYYY-MM-DD')
            })
        }

        props.filter(valueSearch)
        // localStorage.setItem('filter_delivery_order', JSON.stringify(valueSearch))
    }

    const handleReset = () => {
        // localStorage.removeItem('filter_delivery_order');
        setValueSearch({})
        props.filter({})
    }

    return (
        <Collapse defaultActiveKey={["1"]}>
            <Collapse.Panel header={["Filter Delivery Order"]} key="1" >
                <Row>
                    <Col lg={24}>
                        <Row>
                            <Col
                                style={{ padding: '5px' }} lg={6} xs={24}>
                                <Select
                                    mode="multiple"
                                    allowClear
                                    filterOption={false}
                                    style={{ width: '100%' }}
                                    onSearch={handleSearchPo}
                                    onChange={(value) => handleInput(value, 'code')}
                                    placeholder="Code Reservation"
                                    value={valueSearch.code}>

                                    {code.map(item => (
                                        <Option key={item.id} value={item.code}>{item.code}</Option>
                                    ))}
                                </Select>
                            </Col>
                            <Col
                                style={{ padding: '5px' }} lg={6} xs={24}>
                                <Select
                                    mode="multiple"
                                    allowClear
                                    filterOption={false}
                                    style={{ width: '100%' }}
                                    onSearch={handleSearchDo}
                                    onChange={(value) => handleInput(value, 'code_do')}
                                    placeholder="Code Delivery Order"
                                    value={valueSearch.code_do}>

                                    {codeDo.map(item => (
                                        <Option key={item.id} value={item.code}>{item.code}</Option>
                                    ))}
                                </Select>
                            </Col>
                            <Col
                                style={{ padding: '5px' }} lg={6} xs={24}>
                                <Select
                                    mode="multiple"
                                    allowClear
                                    filterOption={false}
                                    style={{ width: '100%' }}
                                    onSearch={handleSearchPlant}
                                    onChange={(value) => handleInput(value, 'plant_id')}
                                    placeholder="Plant"
                                    value={valueSearch.plant_id}>

                                    {plant.map(item => (
                                        <Option key={item.id} value={item.id}>{item.name}</Option>
                                    ))}
                                </Select>
                            </Col>
                            <Col
                                style={{ padding: '5px' }} lg={6} xs={24}>
                                <Select
                                    mode="multiple"
                                    allowClear
                                    filterOption={false}
                                    style={{ width: '100%' }}
                                    onSearch={handleSearchRoom}
                                    onChange={(value) => handleInput(value, 'room_id')}
                                    placeholder="Room"
                                    value={valueSearch.room_id}>

                                    {room.map(item => (
                                        <Option key={item.id} value={item.id}>{item.name}</Option>
                                    ))}
                                </Select>
                            </Col>
                            <Col
                                style={{ padding: '5px' }} lg={6} xs={24}>
                                <Select
                                    mode="multiple"
                                    allowClear
                                    filterOption={false}
                                    style={{ width: '100%' }}
                                    onSearch={handleSearchVendor}
                                    onChange={(value) => handleInput(value, 'vendor_id')}
                                    placeholder="Vendor"
                                    value={valueSearch.vendor_id}>

                                    {vendor.map(item => (
                                        <Option key={item.id} value={item.id}>{item.name}</Option>
                                    ))}
                                </Select>
                            </Col>
                            <Col
                                style={{ padding: '5px' }} lg={6} xs={24}>
                                <Select
                                    mode="multiple"
                                    allowClear
                                    filterOption={false}
                                    style={{ width: '100%' }}
                                    onChange={(value) => handleInput(value, 'type')}
                                    placeholder="Type"
                                    value={valueSearch.type}>
                                    <Option value={0}>Request to Vendor</Option>
                                    <Option value={1}>Transfer with Do</Option>
                                    <Option value={2}>Transfer without Do</Option>
                                </Select>
                            </Col>
                            <Col style={{ padding: '5px' }} lg={6} xs={24}>
                                <RangePicker
                                    onChange={(value) => handleInput(value, 'created_at')}
                                    value={valueSearch.created_at}
                                    placeholder={['Delivery at', 'Delivery at']}
                                    style={{ width: '100%' }} />
                            </Col>
                        </Row>
                    </Col>
                </Row>

                <Space style={{ padding: '5px' }}>
                    <Button type="primary"
                        icon={<SearchOutlined />}
                        onClick={handleFilter}
                    >Search</Button>

                    <Button
                        icon={<ReloadOutlined />}
                        onClick={handleReset}
                    >Reset</Button>
                </Space>
            </Collapse.Panel>
        </Collapse>
    )
}

export default FilterList