import React, { useEffect, useState } from 'react'
import {
    Card, Row, Col, Button,
    Space, Modal, message,
    Table, Tag
} from 'antd';

import {
    EyeOutlined,
    FilePdfOutlined
} from '@ant-design/icons';
import FilterList from './FilterList';
import hasAuthObject from '../../helpers/hasAuthObject';
import Api from '../../helpers/api';
import handleApiError from '../../helpers/handleApiError';
import timeStampsFormat from '../../helpers/dateFormat';
const api = new Api();

// setup auth object config for this page
const authobjectCfg = {
    view: 'delivery-order-view', //button display detail
}

const List = (props) => {

    const [selectedRowKeys, setSelectedRowKeys] = useState([])
    const [selectedRowsData, setSelectedRowsData] = useState([])
    const [loading, setLoading] = useState(false)
    const [data, setData] = useState([])
    const [pagination, setPagination] = useState({})
    const [filters, setFilters] = useState({})

    useEffect(() => {
        fetch({ ...filters })
    }, [filters])


    const fetch = (params = {}) => {
        setLoading(true)
        api.getDeliveryOrderList({ ...params })
            .then(response => {
                const data = response.data;

                let pagination = {}

                pagination.current = data.current_page
                pagination.total = data.total;
                pagination.pageSize = parseInt(data.per_page);

                setData(data.data)
                setPagination(pagination)
                setLoading(false)
            })
            .catch(error => {
                setLoading(false)
                handleApiError(error, props)
            })
    }

    const handleTableChange = (paginate, sorter) => {
        const pager = { ...pagination };
        pager.current = paginate.current;

        setPagination(pager)

        fetch({
            per_page: paginate.pageSize,
            page: paginate.current,
            sort_field: sorter.field,
            sort_order: sorter.order === 'ascend' ? 'asc' : 'desc',
            ...filters,
        });
    }

    const handleFilter = (value) => {
        setFilters(value)
    }

    const handleView = () => {
        if (selectedRowKeys.length > 1) {
            Modal.warning({
                title: `Anda memilih ${selectedRowKeys.length} data, silahkan hanya pilih 1 data`,
            });
            return
        }

        props.history.push(`/delivery-order/view/${selectedRowKeys[0]}`)
    }

    const handlePdf = () => {
        if (selectedRowsData.length > 1) {
            Modal.warning({
                title: `Anda memilih ${selectedRowsData.length} data, silahkan hanya pilih 1 data`,
            });
            return
        }

        let apiUrl = process.env.REACT_APP_API_URL;
        let fullUrl = `${apiUrl}/transaction/delivery-order-pdf/${selectedRowsData[0].code}`
        window.open(fullUrl)
    }

    const handlePdfPO = () => {
        if (selectedRowsData.length > 1) {
            Modal.warning({
                title: `Anda memilih ${selectedRowsData.length} data, silahkan hanya pilih 1 data`,
            });
            return
        }
    
        let pdfPO = selectedRowsData[0].pdf_po
        if (!pdfPO) {
            Modal.warning({
                title: 'Belum memiliki good receive',
            });
            return
        }
        
        window.open(pdfPO)
    }

    const handlePdfGR = () => {
        if (selectedRowsData.length > 1) {
            Modal.warning({
                title: `Anda memilih ${selectedRowsData.length} data, silahkan hanya pilih 1 data`,
            });
            return
        }
    
        let pdfGR = selectedRowsData[0].pdf_gr
        if (!pdfGR) {
            Modal.warning({
                title: 'Belum memiliki good receive',
            });
            return
        }

        window.open(pdfGR)
    }

    //on check table item
    const onSelectChange = (selectedRowKeys, selectedRowsData) => {
        setSelectedRowKeys(selectedRowKeys)
        setSelectedRowsData(selectedRowsData)
    }

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
        // type: 'radio'
    };

    const hasSelected = selectedRowKeys.length > 0;

    const columns = [
        {
            title: 'Status',
            dataIndex: 'status',
            sorter: true,
            render: (status) => {
                switch (status) {
                    case 0:
                        return <Tag color="#fe8b00">Waiting Received</Tag>
                    case 1:
                        return <Tag color="#108ee9">Received</Tag>
                    case 2:
                        return <Tag color="#E20404">Rejected</Tag>
                    default:
                        return null
                }
            },
        },
        // {
        //     title: 'Pdf PO',
        //     dataIndex: 'pdf_po',
        //     sorter: true,
        //     render: (pdf_po) => {
        //         return (
        //             <Button
        //                 type='primary'
        //                 icon={<FilePdfOutlined />}
        //                 onClick={() => window.open(pdf_po)}
        //             />
        //         )
        //     },
        // },
        // {
        //     title: 'Pdf GR',
        //     dataIndex: 'pdf_gr',
        //     sorter: true,
        //     render: (pdf_gr) => {
        //         if (pdf_gr) {
        //             return (
        //                 <Button
        //                     type='primary'
        //                     icon={<FilePdfOutlined />}
        //                     onClick={() => window.open(pdf_gr)}
        //                 />
        //             )
        //         }
        //     },
        // },
        {
            title: 'Code Reservation',
            dataIndex: 'reservation',
            sorter: true,
            render: (reservation) => {
                return reservation ? reservation.code : '-'
            },
        },
        {
            title: 'Code Delivery Order',
            dataIndex: 'code',
            sorter: true,
        },
        {
            title: 'Delivery at',
            dataIndex: 'created_at',
            sorter: true,
            render: (created_at) => {
                return created_at ? timeStampsFormat(created_at) : '-'
            },
        },
        {
            title: 'Approved at',
            dataIndex: 'approved',
            render: (approved, record) => {
                return record.approved_or_rejected_at ? timeStampsFormat(record.approved_or_rejected_at) : '-'
            },
        },
        {
            title: 'Duration',
            dataIndex: 'duration',
            sorter: true,
            render: (duration) => {
                return duration ? duration : '-'
            },
        },
        {
            title: 'Receiver',
            dataIndex: 'reservation',
            sorter: true,
            render: (reservation) => {
                if (reservation) {
                    return reservation.room_receiver
                        ? `${reservation.room_receiver.plant.name} - ${reservation.room_receiver.name}`
                        : '-'
                }
            },
        },
        {
            title: 'Sender',
            dataIndex: 'reservation',
            sorter: true,
            render: (reservation, record) => {
                if (reservation) {
                    return reservation.room_sender
                        ? `${reservation.room_sender.plant.name} - ${reservation.room_sender.name}`
                        : record.reservation.vendor.name
                }
            },
        },
        {
            title: 'Type',
            dataIndex: 'reservation',
            sorter: true,
            render: (reservation) => {
                if (reservation) {
                    switch (reservation.type) {
                        case 0:
                            return <Tag>Request to Vendor</Tag>
                        case 1:
                            return <Tag>Transfer with Do</Tag>
                        case 2:
                            return <Tag>Transfer without Do</Tag>
                        default:
                            return null
                    }
                }
            },
        },
        {
            title: 'Note',
            dataIndex: 'note',
            sorter: true,
        },
    ]

    return (
        <Card>
            <Space direction="vertical" size="large" style={{ width: '100%' }}>
                <Row>
                    <Col lg={24}>
                        <FilterList filter={handleFilter} />
                    </Col>
                </Row>
                <Row>
                    <Col lg={24}>
                        <Space>
                            {hasAuthObject(authobjectCfg.view) &&
                                <Button
                                    onClick={() => handleView()}
                                    disabled={!hasSelected}
                                    icon={<EyeOutlined />}
                                >View</Button>
                            }

                            {hasAuthObject(authobjectCfg.view) &&
                                <Button
                                    onClick={() => handlePdf()}
                                    disabled={!hasSelected}
                                    icon={<FilePdfOutlined />}
                                >DO PDF</Button>
                            }

                            {hasAuthObject(authobjectCfg.view) &&
                                <Button
                                    onClick={() => handlePdfPO()}
                                    disabled={!hasSelected}
                                    icon={<FilePdfOutlined />}
                                >PO PDF</Button>
                            }

                            {hasAuthObject(authobjectCfg.view) &&
                                <Button
                                    onClick={() => handlePdfGR()}
                                    disabled={!hasSelected}
                                    icon={<FilePdfOutlined />}
                                >GR PDF</Button>
                            }
                        </Space>
                    </Col>
                </Row>
                <Row>
                    <Col lg={24} xs={24}>
                        <Table
                            loading={loading}
                            rowSelection={hasAuthObject(authobjectCfg.view || authobjectCfg.update || authobjectCfg.delete) ? rowSelection : null}
                            columns={columns}
                            rowKey={record => record.id}
                            onChange={handleTableChange}
                            dataSource={data}
                            pagination={{
                                pageSizeOptions: ['20', '50', '100', '200'],
                                showSizeChanger: true,
                                showQuickJumper: true,
                                showTotal: (total, range) => `${range[0]}-${range[1]} of ${total} items`,
                                ...pagination
                            }}
                            scroll={{ y: 300, x: 1000 }} />
                    </Col>
                </Row>
            </Space>
        </Card>
    )
}

export default List